<template>
  <article class="content container-fluid" v-if="state.authenticated">
    <section>
      <h1 class="page-header">Hosts</h1>

      <div class="mt-2">
        <div
          class="alert alert-dismissible fade show"
          :class="alert.type"
          role="alert"
          v-if="alert.type"
        >
          <div v-html="alert.message"></div>
          <button
            type="button"
            class="close"
            data-dismiss="alert"
            aria-label="Close"
            @click="alert = {}"
          >
            <span aria-hidden="true">&times;</span>
          </button>
        </div>

        <div class="bg-white table-responsive border border-top-0 rounded">
          <table class="table table-sm">
            <thead>
              <tr>
                <th scope="col" class="p-2" width="30%">Name</th>
                <th scope="col" class="p-2" width="50%">URL</th>
                <th scope="col" width="10%"></th>
                <th scope="col" width="10%"></th>
              </tr>
            </thead>
            <tbody>
              <tr v-for="(host, key) in state.hosts" :key="key">
                <th scope="row">{{ key }}</th>
                <td>{{ host.url }}</td>
                <td class="text-right">
                  <span class="table-icon" v-if="host.accessKey && host.secretKey">
                    <fa icon="lock" class="text-success"></fa>
                  </span>
                  <span class="table-icon" v-if="host.accessKey && host.secretKey">
                    <fa icon="globe" class="text-success"></fa>
                  </span>
                </td>
                <td class="text-right">
                  <a
                    href="javascript:void(0)"
                    @click="open(key)"
                    class="btn btn-xxs btn-outline-success"
                  >Open</a>
                </td>
              </tr>
            </tbody>
          </table>
        </div>
      </div>
    </section>
  </article>
</template>

<script>
export default {
  name: "index",
  props: ['state'],
  components: {},
  data: function () {
    return {
      alert: {}
    }
  },
  beforeMount() {
    if (!this.state.authenticated) {
      this.$router.push('/login')
    }
  },
  methods: {
    open(item) {
      this.$nextTick(function () {
        this.$router.push(`/host/${item}`)
      })
    }
  }
}
</script>

<style lang="scss" scoped>
.table-icon {
  padding: 5px;
  border-radius: 50%;
  background: #e9ecef;
}
</style>
